    import simpleModal from '../../components/simple-pro/customModal.vue';
    import _app from '../../js_sdk/QuShe-SharerPoster/QS-SharePoster/app.js';
    import getSharePoster from '../../js_sdk/QuShe-SharerPoster/QS-SharePoster/QS-SharePoster.js';

    // const plug = uni.requireNativePlugin('Html5app-Baichuan');

    export default {
        data() {
            return {
                logo: "../../static/img/mao.png",
                taobao: "../../static/img/taobao.png",
                buyDes: "口令购买",
                collect: {
                    name: '收藏',
                    isCollect: "",
                },
                show_share: false,
				data:[],
                itemid: '',
                itempic: '',
                itemtitle: '',
                itemprice: '',
                itemendprice: '',
                couponmoney:'',
                couponurl:'',
				itemdesc:'',
                starttime:'',
                endtime:'',
                follow: false,
                loadGoods: true,
                goods: {},
                taoBaoBuy: true,
                shopCartBg: false,
                scrollTop: false,
                couponlist: [],
                navBarButton: false,
                copyTklStatus: false,
                save: false,
                tkl: '',
                relation_id: null,
                money: 0,
                isInvitation: 0,
                showFollow: false,
                isWeiXin: false,
                footprintKey: 'orange-sqx-footprint',
                collectKey: 'orange-sqx-collect',
                poster: {},
                coupon_click_url: "",
                qrShow: false,
                canvasId: 'default_PosterCanvasId'
            };
        },
        components: {
            simpleModal
        },
        onShow: function () {
            this.copyTklStatus = false;
            let relation_id = this.$queue.getData("relation_id");
            if (relation_id) {
                this.relation_id = relation_id;
                this.$queue.setData("erweima",
                    "https://api.qrserver.com/v1/create-qr-code/?size=150x150&data="+this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" +
                    this.itemid + "&relation_id=" + relation_id);
            }
            if (this.$queue.getData("openid")) {
                this.$Request.get("/tao/wx/follow/" + this.$queue.getData("openid")).then(res => {
                    if (res === true) {
                        this.follow = true;
                    } else {
                        this.follow = false;
                    }
                });
            }
        },
        onLoad: function (e) {
            if (e) {










                this.data = JSON.parse(decodeURIComponent(e.data));
                this.itemid = this.data.itemid;
                this.itempic = this.data.itempic;
                this.itemtitle = this.data.itemtitle;
				this.itemprice = this.data.itemprice;
				this.itemendprice = this.data.itemendprice;
                this.starttime = this.data.starttime;
                this.endtime = this.data.endtime;
                this.couponmoney = this.data.couponmoney;
                this.couponurl = this.data.couponurl;
				this.itemdesc = this.data.itemdesc;
                let userId = this.$queue.getData("userId");
                if (userId) {
                    this.$Request.getT("/user/" + userId).then(res => {
                        if (res.status === 0) {
                            this.isInvitation = res.data.isInvitation;
                            this.$queue.setData("isInvitation", res.data.isInvitation);
                            this.$queue.setData("relation", res.data.invitation);
                            this.$queue.setData("grade", res.data.grade);
                            if (res.data.image_url) {
                                this.$queue.setData("image_url", res.data.image_url);
                            }
                            this.$queue.setData("mobile", res.data.phone);
                            this.$queue.setData("nickName", res.data.nickName);
                            this.$queue.setData("relation_id", res.data.relationId);
                            this.$queue.setData("gender", parseInt(res.data.gender));
                        }
                    });
                    this.loadGoodsInfo();
                } else {
                    this.loadGoodsInfo();

                }
            } else {
                uni.navigateBack();
            }
        },
        onPageScroll: function (e) {
            this.scrollTop = e.scrollTop > 200;
        },

        onNavigationBarButtonTap: function () {
            this.navBarButton = !this.navBarButton;
        },
        methods: {
            addPublisher() {
                if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) { //判断iPhone|iPad|iPod|iOS
                    uni.showModal({
                        title: "下载提示",
                        content: '苹果APP正在上架中请先关注公众号使用哦',
                        showCancel: true,
                        success: (e) => {
                            if (e.confirm) {
                                uni.navigateTo({
                                    url: '/pages/member/erweima'
                                });
                            }
                        }
                    });
                } else if (/(Android)/i.test(navigator.userAgent)) { //判断Android
                    window.location.assign("https://fir.vip/d/5yilc")
                } else { //pc
                    window.location.assign(this.$queue.publicYuMing())
                }
            },
            //初始化加载详情数据
            loadGoodsInfo: function () {
                let that = this;
                uni.showLoading({
                    title: '加载中...'
                });
				let apiUrl = this.$Api.goods.iteminfo;
				this.$Request.get(apiUrl,{
					NumIids: that.itemid
				}).then(res => {
                    if (res.Code === 1) {
                        that.goods = res.Message[0];
                        if (that.goods.smallimages && that.goods.smallimages.length > 0) {
                        	let images = that.goods.smallimages;
                        	that.goods.images = [];
                        	for (let i = 0; i < images.length; i++) {
                        		that.goods.images.push(images[i].smallpic);
                        	}
                        } else {
                        	that.goods.images = [];
                        	that.goods.images.push(that.goods.itempic);
                        }
						that.goods.itemprice = that.itemprice;
						that.goods.itemendprice = that.itemendprice;
                        that.goods.starttime = this.formatDate(new Date(that.starttime * 1000));
                        that.goods.endtime = this.formatDate(new Date(that.endtime * 1000));
                        that.goods.couponmoney = that.couponmoney;
                        that.goods.itemsale = that.goods.itemsale > 10000 ? (that.goods.itemsale / 10000).toFixed(1) + '万' : that.goods.itemsale;
						that.goods.itemdesc = that.itemdesc;
						
                        that.loadGoods = true;
                        let grade = this.$queue.getData("grade");
                        if (grade) {
                            that.money = (that.goods.tkmoney * parseFloat(grade)).toFixed(2);
                        } else {
                            that.money = (that.goods.tkmoney * 0.3).toFixed(2);
                        }
                        try {
                            let isExist = this.$queue.isExist(this.collectKey, that.goods.itemid);
                            if (isExist === true) {
                                this.collect.name = "已收藏";
                                this.collect.isCollect = "color: #FC3F78";
                            } else {
                                this.collect.name = "收藏";
                                this.collect.isCollect = false;
                            }
                            let isExists = this.$queue.isExist(this.footprintKey, that.goods.itemid);
                            if (isExists === false) {
                                this.$queue.insert({
                                    key: this.footprintKey,
                                    value: this.goods,
                                });
                            }
                            this.$queue.setData("itempic", that.goods.itempic);
                            this.$queue.setData("itemtitle", that.goods.itemtitle);
                            this.$queue.setData("itemprice", that.goods.itemprice);
                            this.$queue.setData("itemendprice", that.goods.itemendprice);

                            //检查授权会员权限
                            if (this.isInvitation == 0) {
                                this.buyDes = "分享给朋友";
                            } else {
                                let grade = this.$queue.getData("grade");
                                if (grade) {
                                    this.buyDes = "分享（赚" + (this.goods.tkmoney * parseFloat(grade)).toFixed(2) + "元）";
                                } else {
                                    this.buyDes = "分享（赚" + (this.goods.tkmoney * 0.3).toFixed(2) + "元）";
                                }
                            }
                            this.loadTkl();














































                        } catch (e) {
                            console.log(e);
                        }
                    } else {

                        uni.navigateBack();

                    }
                    uni.hideLoading();
                });
                this.$Request.get("/api/get_similar_info/apikey/maxd/itemid/" + this.itemid).then(res => {
                    if (res.code === 1) {
                        res.data.forEach(d => {
                            let grade = this.$queue.getData("grade");
                            if (grade) {
                                d.tkmoney = (d.tkmoney * parseFloat(grade)).toFixed(2);
                            } else {
                                d.tkmoney = (d.tkmoney * 0.3).toFixed(2);
                            }
                            d.itemsale = d.itemsale > 10000 ? "已售 " + (d.itemsale / 10000).toFixed(1) + "万" : "已售 " + d.itemsale;
                            this.couponlist.push(d);
                        });
                    }
                });

            },

            goWeb(url) {
                uni.navigateTo({
                    url: "/pages/member/webview?url=" + url
                });
            },


            erweima() {
                uni.navigateTo({
                    url: '/pages/member/erweima'
                });
            },
            /**
             * 保存图片
             * @param url
             */
            toSave(url) {

                uni.getImageInfo({
                    src: url,
                    success: function (image) {
                        console.log('图片信息：', JSON.stringify(image));
                        uni.saveImageToPhotosAlbum({
                            filePath: image.path,
                            success: function () {
                                console.log('save success');
                                uni.showToast({
                                    title: '图片保存成功',
                                    icon: 'none',
                                    duration: 3000
                                });
                            }
                        });
                    }
                });


            },
            /**
             * 返回
             */
            goBackUp() {
                uni.navigateBack();
            },
            /**
             * 复制链接
             */
            copyHref() {









                let relation_id = this.$queue.getData("relation_id");
                if (relation_id) {
                    uni.setClipboardData({
                        data: this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.itemid + "&relation_id=" + relation_id,
                        success: (r => {
                            this.$queue.showToast("复制成功")
                        }),
                    })
                } else {
                    uni.setClipboardData({
                        data: this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.itemid,
                        success: (r => {
                            this.$queue.showToast("复制成功")
                        }),
                    })
                }


            },
            closeShare() {
                this.show_share = false;
            },
            clickCollect() { //收藏
                let isExist = this.$queue.isExist(this.collectKey, this.goods.itemid);
                if (isExist) {
                    let items = [];
                    items.push(this.goods.itemid);
                    this.$queue.removeItem(this.collectKey, items);
                    this.collect.name = "收藏";
                    this.collect.isCollect = "";
                } else {
                    this.$queue.insert({
                        key: this.collectKey,
                        value: this.goods,
                    });
                    this.collect.name = "已收藏";
                    this.collect.isCollect = "color: #FC3F78";
                }
            },
            getQuan: function () {
                uni.showLoading({
                    title: '加载中...'
                });
                let that = this;
                this.$Request.post("/api/ratesurl", {
                    itemid: this.itemid,
                    tb_name: this.$queue.getTaoBaoName(),
                    pid: this.$queue.getTaoBaoPid(),
                    apikey: this.$queue.getTaoBaoKey()
                }).then(res => {
                    if (res.code === 1) {
                        let coupon_click_url;
                        let relation_id = that.$queue.getData("relation_id");
                        let relation = that.$queue.getData("relation");
                        if (relation_id || relation) {
                            if (relation) {
                                coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation
                            }
                            if (relation_id) {
                                coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation_id
                            }


























                            that.shopCartShare('tttt');

                        } else {
                            this.loginS();
                        }
                        that.coupon_click_url = coupon_click_url;
                    }
                });
            },
            /**
             *获取佣金结算
             * @param type
             */
            getTkl: function (type) {
                let that = this;
                let relation_id = that.$queue.getData("relation_id");
                if (this.tkl) {
                    if (type === "tkl" && type !== "tklbuy") {
                        this.$refs.simpleModalTkl.show({
                            showConfirmButton: false,
                        });
                    } else if (type === "tttt") {
                        this.$refs.simpleModalTklBuy.show({
                            showConfirmButton: false,
                        });
                    } else if (type === "copy") {
                        that.copyTkl();
                    } else if (type === "save") {
                        //that.copyTkl();
                    } else if (relation_id) {
                        this.$refs.simpleModalTkl.show({
                            showConfirmButton: false,
                        });
                    } else {
                        this.$refs.simpleModalTklBuy.show({
                            showConfirmButton: false,
                        });
                    }
                    uni.hideLoading();
                } else {
                    uni.showLoading({
                        title: '加载中...'
                    });
                    this.$Request.post("/api/ratesurl", {
                        itemid: this.itemid,
                        tb_name: this.$queue.getTaoBaoName(),
                        pid: this.$queue.getTaoBaoPid(),
                        apikey: this.$queue.getTaoBaoKey()
                    }).then(res => {
                        if (res.code === 1) {
                            let relation = that.$queue.getData("relation");






                            let coupon_click_url;
                            if (relation_id || relation) {
                                if (relation) {
                                    coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation
                                }
                                if (relation_id) {
                                    coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation_id
                                }
                            } else {
                                coupon_click_url = res.data.coupon_click_url
                            }
                            this.$Request.postF("/tao/taobao/tbwd", {
                                appKey: this.$queue.getTaoBaoAppid(),
                                appSecret: this.$queue.getTaoBaoSecret(),
                                logo: this.goods.itempic,
                                text: this.goods.itemtitle,
                                url: coupon_click_url
                            }).then(res => {
                                if (res && res.tbk_tpwd_create_response.data.model) {
                                    this.tkl = res.tbk_tpwd_create_response.data.model;
                                    if (type === "tkl" && type !== "tklbuy") {
                                        this.$refs.simpleModalTkl.show({
                                            showConfirmButton: false,
                                        });
                                    } else if (type === "tttt") {
                                        this.$refs.simpleModalTklBuy.show({
                                            showConfirmButton: false,
                                        });
                                    } else if (type === "copy") {
                                        that.copyTkl();
                                    } else if (type === "save") {
                                        //that.copyTkl();
                                    } else if (relation_id) {
                                        this.$refs.simpleModalTkl.show({
                                            showConfirmButton: false,
                                        });
                                    } else {
                                        this.$refs.simpleModalTklBuy.show({
                                            showConfirmButton: false,
                                        });
                                    }
                                } else {
                                    uni.showToast({
                                        title: "请关注微信公众号【省钱兄】领取优惠券",
                                        mask: false,
                                        duration: 1500,
                                        icon: "none"
                                    });
                                }
                                uni.hideLoading();
                            }).catch(res => {
                                uni.hideLoading();
                                uni.showToast({
                                    title: "请关注微信公众号【省钱兄】领取优惠券",
                                    mask: false,
                                    duration: 1500,
                                    icon: "none"
                                });
                            });
                        } else {
                            uni.hideLoading();
                        }
                    });
                }
            },

            formatDate: function (now) {
                const year = now.getFullYear();
                const month = now.getMonth() + 1;
                const date = now.getDate();
                return year + "-" + month + "-" + date;
            },
            loadTkl() {
                let that = this;
                this.$Request.post("/api/ratesurl", {
                    itemid: this.itemid,
                    tb_name: this.$queue.getTaoBaoName(),
                    pid: this.$queue.getTaoBaoPid(),
                    apikey: this.$queue.getTaoBaoKey()
                }).then(res => {
                    if (res.code === 1) {
                        let relation_id = that.$queue.getData("relation_id");
                        let relation = that.$queue.getData("relation");






                        let coupon_click_url;
                        if (relation_id || relation) {
                            if (relation) {
                                coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation
                            }
                            if (relation_id) {
                                coupon_click_url = res.data.coupon_click_url + "&relationId=" + relation_id
                            }
                        } else {
                            coupon_click_url = res.data.coupon_click_url
                        }
                        this.coupon_click_url = coupon_click_url;
                        this.$Request.postF("/tao/taobao/tbwd", {
                            appKey: this.$queue.getTaoBaoAppid(),
                            appSecret: this.$queue.getTaoBaoSecret(),
                            logo: this.goods.itempic,
                            text: this.goods.itemtitle,
                            url: coupon_click_url
                        }).then(res => {
                            if (res && res.tbk_tpwd_create_response.data.model) {
                                this.tkl = res.tbk_tpwd_create_response.data.model;
                                // uni.setClipboardData({
                                //     data: res.tbk_tpwd_create_response.data.model,
                                //     success: (r => {
                                //     }),
                                // })
                            }
                        })
                    }
                });
            },
            copyTkl: function () {
                let relation = this.$queue.getData("relation");
                if (relation) {
                    uni.setClipboardData({
                        data: this.tkl,
                        success: (r => {
                            uni.showToast({
                                title: "复制成功",
                                duration: 1500,
                            });
                        }),
                    })
                } else {
                    this.loginS();
                }
            },
            haibao: function () {

                this.shareFc();






            },
            moveHandleStop: function () {
                console.log("111")
            },
            showShopCartBg: function (type) {
                if (type === "cart") {
                    this.shopCartBg = !this.shopCartBg;
                }
                if (type === "nav") {
                    this.navBarButton = !this.navBarButton;
                }
            },
            topScrollTap: function () {
                uni.pageScrollTo({
                    scrollTop: 0,
                    duration: 300
                });
            },
            navBarButtontO: function (type) {
                if (type === "search") {
                    uni.switchTab({
                        url: "/pages/homeSearch/index"
                    })
                } else if (type === "like") {
                    uni.switchTab({
                        url: "/pages/member/user"
                    })
                } else if (type === "footer") {
                    uni.navigateTo({
                        url: "/pages/footer/index"
                    })
                } else if (type === "home") {
                    uni.switchTab({
                        url: "/pages/index/index"
                    })
                }

            },
            followWeChat() {
                let ua = navigator.userAgent.toLowerCase();
                if (ua.indexOf('micromessenger') !== -1) {
                    if (!window.WeixinJSBridge || !WeixinJSBridge.invoke) {
                        document.addEventListener('WeixinJSBridgeReady', ready, false)
                    } else {
                        if (this.$queue.getData("openid")) {
                            this.$Request.get("/tao/wx/follow/" + this.$queue.getData("openid")).then(res => {
                                if (res !== true) {
                                    localStorage.openUrl = window.location.href;
                                    uni.navigateTo({
                                        url: "/pages/member/erweima"
                                    })
                                }
                            });
                        }
                    }
                }
            },
            shopCartShare: function (type) {
                if (type === "tkl") {
                    this.copyTklStatus = false;
                    this.getTkl(type);
                } else if (type === "tklbuy") {
                    let relation_id = this.$queue.getData("relation_id");
                    if (relation_id) {
                        // // 淘口令购买
                        this.copyTklStatus = false;
                        this.getTkl(type);
                    } else {
                        if (this.isInvitation == 0) {
                            this.$refs.simpleModalTkl.show({
                                showConfirmButton: false,
                            });
                        } else {
                            this.$queue.setData("href", "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)));
                            let token = this.$queue.getData("token");
                            let userId = this.$queue.getData("userId");
                            if (token) {
                                uni.navigateTo({
                                    url: "/pages/member/publisher?uid=" + userId + "&token=" + token
                                });
                            } else {
                                this.loginS();
                            }
                        }

                    }
                } else if (type === "showPic") {
                    let relation_id = this.$queue.getData("relation_id");
                    if (relation_id) {
                        this.shopCartBg = true;
                    } else {
                        if (this.isInvitation == 0) {
                            this.shopCartBg = true;
                        } else {
                            this.$queue.setData("href", "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)));
                            let token = this.$queue.getData("token");
                            let userId = this.$queue.getData("userId");
                            if (token) {
                                uni.navigateTo({
                                    url: "/pages/member/publisher?uid=" + userId + "&token=" + token
                                });
                            } else {
                                this.loginS();
                            }
                        }
                    }
                } else if (type === "quan") {
                    let relation_id = this.$queue.getData("relation_id");
                    let userId = this.$queue.getData("userId");
                    if (userId) {
                        if (relation_id) {
                            this.getQuan();
                        } else {
                            this.goPublisher(e);
                        }
                    } else {
                        this.loginS(e);
                    }
                } else {
                    this.copyTklStatus = false;
                    this.getTkl(type);
                }
            },
            goPublisher(e) {
                let token = this.$queue.getData("token");
                let userId = this.$queue.getData("userId");
                if (token) {
                    this.$queue.setData("href", "/pages/detail/goodsinfos?data=" + encodeURIComponent(JSON.stringify(e)));
                    uni.navigateTo({
                        url: "/pages/member/publisher?uid=" + userId + "&token=" + token
                    });
                } else {
                    this.loginS(e);
                }
            },

            loginS(e) {
















                uni.showModal({
                    title: '登录提醒',
                    showCancel: false,
                    content: '当前账号未登录\n请登录后领取优惠券',
                    success: (confirmRes) => {
                        if (confirmRes.confirm) {
                            this.$queue.setData("href", '/pages/detail/goodsinfos?itemid=' + this.itemid);
                            uni.navigateTo({
                                url: '/pages/public/login'
                            });
                        }
                    }
                });

            },
            shopCartShares: function (type) {























                // tkl = 淘口令  picture = 图片
                if (type === "tkl") {
                    let relation_id = this.$queue.getData("relation_id");
                    if (relation_id) {
                        // // 淘口令购买
                        this.copyTklStatus = false;
                        this.getTkl(type);
                    } else {
                        let token = this.$queue.getData("token");
                        let userId = this.$queue.getData("userId");
                        if (token) {
                            uni.navigateTo({
                                url: '/pages/member/publisher?uid=' + userId + '&token=' + token
                            });
                        } else {
                            this.loginS();
                        }
                    }
                } else if (type === "tklbuy") {
                    // // 淘口令购买
                    this.copyTklStatus = false;
                    this.getTkl(type);
                } else if (type === "quan") {
                    this.getQuan();
                }

            },
            TklmaskClose: function (e) {
                this.$refs.simpleModalTkl.hide();
                this.copyTklStatus = false;
            },
            copyTklWenAn: function () {
                uni.setClipboardData({
                    data: "椱ァ製这段文字，打开【◇綯℡寳】即可领券购买。$" + this.tkl,
                    success: (r => {
                        this.copyTklStatus = true;
                    }),
                })
            },
            copyTklWenAns: function () {
                //
                // for (let i = 0; i < this.goods.images.length; i++) {
                //     this.toSave(this.goods.images[i])
                // }
                //
                let relation_id = this.$queue.getData("relation_id");











                uni.setClipboardData({
                    data: this.goods.itemtitle + "\n【在售价】" + this.goods.itemprice + "元\n【券后价】" + this.goods.itemendprice +
                        "元\n【必买理由】\n" + this.goods.itemdesc + "+\n$" + this.tkl + "椱ァ製此信息进入【◇綯℡寳】即可查看并下单\n",
                    success: (r => {
                        this.copyTklStatus = true;
                    })
                })


            },
            TklBuymaskClose: function () {
                this.$refs.simpleModalTklBuy.hide();
                this.copyTklStatus = false;
            },
            async shareFc() {
                if (!this.goods.itempic_copy) {
                    _app.showToast('当前商品不支持海报生成');
                    return;
                }
                try {
                    if (!this.poster.finalPath) {
                        const d = await getSharePoster({
                            type: 'testShareType',
                            backgroundImage: this.goods.itempic,
                            posterCanvasId: this.canvasId,
                            qrCodeArray: ({
                                              bgObj,
                                              type,
                                              bgScale
                                          }) => {
                                let relation_id = this.$queue.getData("relation_id");
                                if (relation_id) {
                                    return [{
                                        text: this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.itemid + "&relation_id=" +
                                            relation_id,
                                        size: bgObj.width * 0.2,
                                        dx: bgObj.width * 0.05,
                                        dy: bgObj.height - bgObj.width * 0.25
                                    }]
                                } else {
                                    return [{
                                        text: this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.itemid,
                                        size: bgObj.width * 0.2,
                                        dx: bgObj.width * 0.05,
                                        dy: bgObj.height - bgObj.width * 0.25
                                    }]
                                }
                            },
                            textArray: ({
                                            bgObj,
                                            type,
                                            bgScale
                                        }) => {
                                const fontSize = bgObj.width * 0.05;
                                const fontSize2 = bgObj.width * 0.08;
                                const widths = bgObj.width * 0.25;
                                const fontSize1 = bgObj.width * 0.04;
                                const lineHeight = bgObj.height * 0.04;
                                return [{
                                    fontStyle: 'italic',
                                    fontWeight: 'bold',
                                    text: "【省钱兄】券后价",
                                    size: fontSize,
                                    color: 'red',
                                    alpha: '1',
                                    textAlign: 'left',
                                    textBaseline: 'middle',
                                    infoCallBack(textLength) {
                                        _app.log('index页面的text的infocallback ，textlength:' + textLength);
                                        return {
                                            dx: bgObj.width - textLength - fontSize,
                                            dy: bgObj.height - lineHeight * 3.5
                                        }
                                    }
                                },
                                    {
                                        fontWeight: 'bold',
                                        text: '长按识别二维码领券',
                                        size: fontSize1,
                                        color: 'white',
                                        alpha: 1,
                                        textAlign: 'right',
                                        textBaseline: 'middle',
                                        infoCallBack(textLength) {
                                            return {
                                                dx: bgObj.width * 0.37,
                                                dy: bgObj.height - bgObj.width * 0.02
                                            }
                                        }
                                    },
                                    {
                                        text: this.goods.itemendprice + ' 元',
                                        size: fontSize2,
                                        fontWeight: 'bold',
                                        color: 'red',
                                        alpha: 1,
                                        textAlign: 'left',
                                        textBaseline: 'middle',
                                        infoCallBack(textLength) {
                                            return {
                                                dx: bgObj.width - textLength - fontSize,
                                                dy: bgObj.height - lineHeight * 1.5
                                            }
                                        }
                                    },
                                ]
                            },
                            setCanvasWH: ({
                                              bgObj,
                                              type,
                                              bgScale
                                          }) => { // 为动态设置画布宽高的方法，
                                this.poster = bgObj;
                            },
                            setDraw: ({
                                          Context,
                                          bgObj,
                                          type,
                                          bgScale
                                      }) => {
                                Context.setFillStyle('black');
                                Context.setGlobalAlpha(0.5);
                                Context.fillRect(0, bgObj.height - bgObj.height * 0.2, bgObj.width, bgObj.height * 0.2);
                            }
                        });
                        console.log('海报生成成功， 临时路径: ' + d.poster.tempFilePath)
                        this.poster.finalPath = d.poster.tempFilePath;
                    }
                    this.qrShow = true;
                } catch (e) {
                    _app.hideLoading();
                    _app.showToast(JSON.stringify(e));
                    console.log(JSON.stringify(e));
                }
            },
            saveImage() {

                uni.saveImageToPhotosAlbum({
                    filePath: this.poster.finalPath,
                    success(res) {
                        _app.showToast('保存成功');
                    }
                });




            },
            share() {





                _app.showToast('请长按图片保存后自行分享');

            },
            hideQr() {
                this.qrShow = false;
            }
        }
    };
